
.page-error {
  color: #707473;
  text-align: center;
}
.page-error .box {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 360px;
    transform: translate(-50%, -50%);
}
.page-error .pic {
    float: left;
}
.page-error .info {
    float: right;
}
.page-error img {
    width: 157px;
}
.page-error h1 {
    font-size: 64px;
}
.page-error h4 {
    font-size: 16px;
    margin-bottom: 20px;
}



